import React, { useEffect, useState } from "react";
import axios from "axios";
import "./style.css";
import logo from './logo-image.png';

const App = () => {
  const [cardData, setCardData] = useState([]);
  const [visible, setVisible] = useState(0);

  const allCardData = async () => {
    const response = await axios.get("https://reqres.in/api/users?page=1");
    setCardData(response.data.data);
  };

  const loadMore = () => {
    setVisible(visible +6);
  };
  const loadLess = () => {
    setVisible(0);
  };


  useEffect(() => {
    allCardData();
  }, []);

  const renderCard = (person, index) => {
    return (
    //  box displaying users data
      <div className="box-data">
        <div className="box-item">
         <img variant="top" src={person.avatar}/>
         <div className="info">
        <h2>{person.first_name} {person.last_name}</h2>
        
          <p>Email:{person.email}</p>
          </div>
      </div>  
      
      </div>
    );
  };

  return (
    // header
    <div className="App-body">
      <div className="navbar">
        <div className="header">
          <h1>Zingyfy</h1>
          <img className="logo-image" src={logo}/>
         
        </div>
        <div className="nav-button">
        <button class="btn" onClick={loadMore}>Get User Data</button>
      <button class="btn" onClick={loadLess}>Hide</button>
      </div>
      </div>
     
      <div className="wrapper-box">
     
        <div className="box">
          {cardData.slice(0, visible).map(renderCard)}
        </div>
      </div>
      
       
    </div>
  );
};

export default App;